/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable react/no-unknown-property */
import React, { Suspense } from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls } from '@react-three/drei';

import { useDispatch, useSelector } from 'react-redux';
import { dataByState } from '../Store/dataState';
import { setPopupShow } from '../Store/popupState';
import Button from '../Components/Button';
import CityBackground from '../Components/BuildingOverview/Models/CityBackgound';
import Building from '../Components/BuildingOverview/Models/Building';
import FloorOverview from '../Components/BuildingOverview/FloorOverview';
import ConceptDetailPopup from './Popup/ConceptDetailPopup';

import buildingCoreConceptData from '../Db/buildingCoreConceptData.json';
import assetsData from '../Db/assets.json';

export default function City() {
  const dispatch = useDispatch();
  const zoomState = useSelector((state) => state.zoomState.value);
  const dataValue = useSelector((state) => state.data.value);
  return (
    <div className="city">
      <div className="model-container">
        <Canvas
          camera={{
            fov: 10,
            position: [-60, 80, 100],
          }}
        >
          <Suspense fallback={null}>
            <directionalLight intensity={2} />
            <ambientLight intensity={4} />
            <Building />
            <CityBackground />
            <OrbitControls
              enablePan
              enableZoom
              enableRotate
              enableDamping
              minPolarAngle={0}
              maxPolarAngle={Math.PI * 0.5}
            />
          </Suspense>
        </Canvas>
        <FloorOverview />
      </div>
      <div className={`city-nav ${zoomState ? 'hide-city-nav' : ''}`}>
        <Button
          buttonName="building-concept-button"
          icon={assetsData.icons[0].conceptIcon}
          clickEvent={() => {
            dispatch(setPopupShow());
            dispatch(dataByState(buildingCoreConceptData));
          }}
        />
      </div>
      <ConceptDetailPopup dataList={dataValue} />
    </div>
  );
}
