import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Link, useLocation } from 'react-router-dom';

import Button from './Button';

import assetsData from '../Db/assets.json';

export default function PopupNavigation() {
  const location = useLocation();
  const popup = useSelector((state) => state.popup.value);
  const [show, showState] = useState('flex');
  useEffect(() => {
    if (location.pathname === '/intro'
    ) {
      showState('none');
    } else if (popup) {
      showState('none');
    } else {
      showState('flex');
    }
  }, [location, showState, popup]);
  return (
    <div
      className="navigation"
      style={{
        display: show,
      }}
    >
      <div className="button-container">
        <div className="logo-box">
          <Link to="/">
            <Button
              buttonName="home-link-button"
              icon={assetsData.icons[0].brandLogo}
            />
          </Link>
        </div>
        <div
          className="session-box"
          style={{
            display: location.pathname === '/concept'
            || location.pathname === '/question' ? 'none' : 'flex',
          }}
        >
          <Link to="/intro">
            <Button
              buttonName="cover-link-button"
              icon={assetsData.icons[0].coverIcon}
            />
          </Link>
          <Link to="/video">
            <Button
              buttonName="video-link-button"
              icon={assetsData.icons[0].tIcon}
            />
          </Link>
          <Link to="/question">
            <Button
              buttonName="question-button"
              icon={assetsData.icons[0].qIcon}
            />
          </Link>
          <Link to="/concept">
            <Button
              buttonName="session-01-link-button"
              icon={assetsData.icons[0].s1Icon}
            />
          </Link>
          <Link to="/city">
            <Button
              buttonName="session-02-link-button"
              icon={assetsData.icons[0].s2Icon}
            />
          </Link>
        </div>
      </div>
    </div>
  );
}
