/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import zoneData from '../Db/zoneData.json';

export const valueGroup = createSlice({
  name: 'valueGroup',
  initialState: {
    totalFloorValue: zoneData.zone.length - 1,
    moveValue: 2.5,
  },
});
export default valueGroup.reducer;
