import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

import FloorElevator from './FloorController/FloorElevator';
import FloorController from './FloorController/FloorController';

import asset from '../../Db/assets.json';
import zoneData from '../../Db/zoneData.json';

export default function FloorInfo({
  setFloorValue,
  setFloorHeight,
  setPopup,
  floorValue,
  floorHeight,
  popup,
}) {
  const totalFloorValue = useSelector((state) => state.valueGroup.totalFloorValue);

  return (
    <div className="floor-information">
      <div className="button-pre">
        <Link to="/city">
          <img src={process.env.PUBLIC_URL + asset.icons[0].pre} alt="pre" />
        </Link>
      </div>
      {
        zoneData.zone.map((data, i) => (
          <div
            className={`
              floor-information-container
              ${i === (totalFloorValue - floorValue)
              ? 'left-slide'
              : 'left-slide-hidden'}
            `}
            key={data.number}
          >
            <div className="floor-number">
              <h1 className={`rolling-title heading-large ${i === (totalFloorValue - floorValue) ? 'rolling' : ''}`}>
                {data.number}
              </h1>
            </div>
            <h2 className="floor-title sub-heading">
              {data.title}
            </h2>
            <p className="floor-description body-small">
              {data.longDesc}
            </p>
          </div>
        ))
      }
      <FloorElevator
        setFloorHeight={setFloorHeight}
        setFloorValue={setFloorValue}
        setPopup={setPopup}
        floorValue={floorValue}
        popup={popup}
      />
      <FloorController
        setFloorValue={setFloorValue}
        setFloorHeight={setFloorHeight}
        setPopup={setPopup}
        floorValue={floorValue}
        floorHeight={floorHeight}
        popup={popup}
      />
    </div>
  );
}
