import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setFadeTrue } from '../../Store/fadeState';

import zoneData from '../../Db/zoneData.json';

import { dataByState } from '../../Store/dataState';
import { setPopupShow } from '../../Store/popupState';

export default function FloorMap({
  // setPopup,
  floorValue,
}) {
  const dispatch = useDispatch();
  const totalFloorValue = useSelector((state) => state.valueGroup.totalFloorValue);
  const currentFloor = Math.abs(floorValue - totalFloorValue);

  return (
    <div className="floor-map">
      {zoneData.zone.map((data, i) => (
        <div className={`floor-content ${currentFloor === i ? 'show-floor-content' : ''}`}>
          <img src={process.env.PUBLIC_URL + data.isometric} alt={data.title} />

          <div className="pins">
            {data.specification?.map((el, num) => (
              <div
                className={`pin ${zoneData.zone[Math.abs(floorValue - totalFloorValue)].title.replace(/[' ']/g, '-')}-pin-${num + 1}`}
                onClick={() => {
                  // setPopup(num);
                  dispatch(setFadeTrue());
                  dispatch(dataByState(el));
                  dispatch(setPopupShow());
                }}
                role="presentation"
              >
                {el.info.icon ? (
                  <i className="pin-icon">
                    <img src={process.env.PUBLIC_URL + el.info.icon} alt={el.info.title} />
                  </i>
                ) : null}
                {el.info.title ? (
                  <p className="pin-title body-large">
                    {el.info.title}
                  </p>
                ) : null}
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
}
