import React from 'react';

import { useDispatch, useSelector } from 'react-redux';
import { setPopupHide } from '../../Store/popupState';
import ImageTextPagenation from '../../Template/ImageTextPagenation';

import Button from '../../Components/Button';
import { pageByState } from '../../Store/pageState';

import assetsData from '../../Db/assets.json';

export default function Home({ dataList }) {
  const dispatch = useDispatch();
  const popup = useSelector((state) => state.popup.value);
  return (
    <div className={`concept-detail-popup ${popup ? 'show-popup' : 'hide-popup'}`}>
      <Button
        buttonName="popup-close"
        icon={assetsData.icons[0].close}
        clickEvent={() => {
          dispatch(setPopupHide());
          setTimeout(dispatch(pageByState(0)), 1000);
        }}
      />
      <ImageTextPagenation dataList={dataList} />
    </div>
  );
}
