import React from 'react';

import { useDispatch } from 'react-redux';
import { setZoomStateFalse } from '../../../Store/zoomState';
import { setHomePopupTrue } from '../../../Store/homePopupState';

import { setPopupShow } from '../../../Store/popupState';
import { dataByState } from '../../../Store/dataState';

import asset from '../../../Db/assets.json';

import buildingOptionAdata from '../../../Db/buildingOptionAdata.json';
import buildingOptionBdata from '../../../Db/buildingOptionBdata.json';
import buildingOptionCdata from '../../../Db/buildingOptionCdata.json';
import lightingConceptData from '../../../Db/lightingConceptData.json';
import FAndBConceptData from '../../../Db/FAndBConceptData.json';
import buildingComparisonData from '../../../Db/buildingComparisonData.json';
import interiorConceptData from '../../../Db/interiorConceptData.json';
import Button from '../../Button';

export default function HomeNav({ setOverviewState }) {
  const dispatch = useDispatch();
  return (
    <div className="home-navigation">
      <div
        className="button back-button"
        onClick={() => {
          dispatch(setZoomStateFalse());
          setOverviewState(6);
        }}
        role="presentation"
      >
        <img src={process.env.PUBLIC_URL + asset.icons[0].pre} alt="pre" />
      </div>
      <Button
        clickEvent={() => {
          dispatch(dataByState(buildingOptionAdata));
          dispatch(setPopupShow());
        }}
        icon={asset.icons[0].aIcon}
      />
      <Button
        clickEvent={() => {
          dispatch(dataByState(buildingOptionBdata));
          dispatch(setPopupShow());
        }}
        icon={asset.icons[0].bIcon}
      />
      <Button
        clickEvent={() => {
          dispatch(dataByState(buildingOptionCdata));
          dispatch(setPopupShow());
        }}
        icon={asset.icons[0].coverIcon}
      />
      <Button
        clickEvent={() => {
          dispatch(dataByState(buildingComparisonData));
          dispatch(setPopupShow());
        }}
        icon={asset.icons[0].listIcon}
      />
      <Button
        clickEvent={() => {
          dispatch(dataByState(interiorConceptData));
          dispatch(setPopupShow());
        }}
        icon={asset.icons[0].conceptIcon}
      />
      <Button
        clickEvent={() => {
          dispatch(dataByState(lightingConceptData));
          dispatch(setPopupShow());
        }}
        icon={asset.icons[0].conceptIcon}
      />
      <Button
        clickEvent={() => {
          dispatch(dataByState(FAndBConceptData));
          dispatch(setPopupShow());
        }}
        icon={asset.icons[0].conceptIcon}
      />
      <div
        className="button popup-button popup-closed"
        onClick={() => {
          dispatch(setHomePopupTrue());
        }}
        role="presentation"
      >
        <img src={process.env.PUBLIC_URL + asset.icons[0].zone} alt="zone" />
      </div>
    </div>
  );
}
