/* eslint-disable max-len */
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

import HomeNav from './HomeNavigation/HomeNav';
import HomePopup from './HomePopup/HomePopup';
import ConceptDetailPopup from '../../Pages/Popup/ConceptDetailPopup';

import zoneData from '../../Db/zoneData.json';

import assetsData from '../../Db/assets.json';

export default function SectionInfo() {
  const zoomState = useSelector((state) => state.zoomState.value);
  const dataValue = useSelector((state) => state.data.value);

  const [visible, setVisible] = useState(0);
  const [showBackground, setShowBackground] = useState(false);
  const [overviewState, setOverviewState] = useState(6);

  useEffect(() => {
    if (zoomState) {
      setTimeout(() => {
        setVisible('fade-in');
      }, 2200);
    } else {
      setVisible('fade-out');
    }
  }, [zoomState]);

  console.log(overviewState);

  return (
    <div className={`section-information ${visible}`}>
      <div
        className={`background-color ${showBackground ? 'show-background-color' : ''}`}
        style={{
          background: zoneData.zone[overviewState].color,
        }}
      />
      <HomeNav
        setOverviewState={setOverviewState}
      />
      <HomePopup />
      <div className="floor-container">
        {
          zoneData.zone.map((data, i) => (
            <div
              className="zone"
              role="presentation"
              key={data.number}
              onClick={() => {
                localStorage.setItem('value', zoneData.zone.length - (i + 1));
              }}
              onMouseEnter={() => {
                setOverviewState(i);
              }}
            >
              <Link to="/city/floors">
                <div
                  className="pin-title"
                  onMouseEnter={() => {
                    setShowBackground(true);
                  }}
                >
                  <p className="body-medium">
                    {data.title}
                  </p>
                </div>
              </Link>
              <hr className="pin-line" />
            </div>
          ))
        }
      </div>
      <div className="section-map">
        <div className="zone-highlight-container">
          {
            zoneData.zone.map((data, i) => (
              <div
                className={
                  `zone-highlight
                  zone-highlight-${i}
                  ${overviewState === i
                    ? 'show-highlight' : ''}`
                  }
                style={{
                  background: zoneData.zone[i].color,
                }}
                key={data.number}
              />
            ))
          }
        </div>
        <img src={process.env.PUBLIC_URL + assetsData.image[0].sectionView} alt="section-map" />
      </div>

      <div className="zone-brief-container">
        {
            zoneData.zone.map((data, i) => (
              <div className={`zone-brief ${overviewState === i ? 'show-brief' : ''}`}>
                <h2 className="sub-heading">
                  {data.number}
                </h2>
                <h3 className="heading-medium">
                  {data.title}
                </h3>
                <div className="brief-body-content">
                  <div className="icon">
                    <img src={process.env.PUBLIC_URL + data.icon} alt={data.number} />
                  </div>
                  <p className="body-medium">
                    {data.desc}
                  </p>
                  <div className="zone-illustration">
                    <img src={process.env.PUBLIC_URL + data.illustration} alt="" />
                  </div>
                </div>
              </div>
            ))
          }
      </div>
      <ConceptDetailPopup dataList={dataValue} />
    </div>
  );
}
